.footer-panel {
  grid-area: footer-panel;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0; }
  .footer-panel .email-signup {
    margin-top: 24px;
    margin-bottom: 16px; }
  .footer-panel h6 {
    color: #fff; }
  .footer-panel a.email-link {
    text-decoration: none;
    margin-bottom: 8px; }
    .footer-panel a.email-link h4 {
      font-weight: bold; }
      .footer-panel a.email-link h4:hover {
        color: #EBD3D5; }
  .footer-panel .footer-cta-title {
    max-width: 500px;
    margin: 0 1.5px 0 1.5px; }
  .footer-panel .footer-social-row {
    display: flex; }
    .footer-panel .footer-social-row button {
      margin: 30px 15px; }
      @media only screen and (max-width: 375px) {
        .footer-panel .footer-social-row button {
          margin: 20px 10px; } }
      .footer-panel .footer-social-row button svg {
        width: 50px;
        height: 50px;
        margin: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); }
  .footer-panel p, .footer-panel h2, .footer-panel a {
    color: white;
    margin: 5px 0 5px 0;
    font-size: 20px; }
    @media only screen and (max-width: 768px) {
      .footer-panel p, .footer-panel h2, .footer-panel a {
        font-size: 16px; } }
    @media only screen and (max-width: 375px) {
      .footer-panel p, .footer-panel h2, .footer-panel a {
        font-size: 13px; } }
  .footer-panel .footer-copyright {
    font-size: 15px; }
    @media only screen and (max-width: 768px) {
      .footer-panel .footer-copyright {
        font-size: 13px; } }
    @media only screen and (max-width: 375px) {
      .footer-panel .footer-copyright {
        font-size: 10px; } }
